import type { FC } from 'react';
import React, { useContext } from 'react';
import { WebchatContext } from '@botonic/react';
import { ThemeProps } from '@botonic/react/src/components';
import clsx from 'clsx';
import styles from './CustomMessage.module.css';
// eslint-disable-next-line css-modules/no-unused-class
import commonStyles from '../../styles/common.module.css';
import TextLink from '../../../TextLink';

interface Props {
  message: string;
  theme: ThemeProps;
  isBubble: boolean;
}

export const CustomMessage: FC = React.forwardRef((props: Props, ref) => {
  const { message, theme, isBubble } = props;
  const {
    webchatState: { theme: themeConfig },
  } = useContext(WebchatContext);

  return (
    <div className={styles.customMessage} ref={ref}>
      <div className={styles.avatarWrapper}>
        <div
          style={{
            // This is a problem inside the @botonic ThemeProps component, the imageStyle taken internally
            ...theme?.message?.bot?.imageStyle,
            margin: 0,
            background: isBubble ? theme?.message?.bot?.imageStyle?.background : 'none',
          }}
        />
      </div>

      <div className={styles.wrapper}>
        <div
          className={clsx(styles.bubbleText, {
            [styles.noBubbleText]: !isBubble,
          })}
          style={
            isBubble
              ? {
                  fontSize: themeConfig?.initialScreen?.customMessageFontSize,
                  color: themeConfig?.initialScreen?.customMessageColor,
                  fontFamily: themeConfig?.initialScreen?.customMessageFontFamily,
                  backgroundColor: themeConfig?.initialScreen?.customMessageBackgroundColor,
                }
              : {
                  fontSize: themeConfig?.initialScreen?.customMessageNoBubbleFontSize,
                  color: themeConfig?.initialScreen?.customMessageNoBubbleColor,
                  fontFamily: themeConfig?.initialScreen?.customMessageNoBubbleFontFamily,
                }
          }
          data-qa="test-custom-message"
        >
          <TextLink target={message} />
        </div>
      </div>

      <div className={commonStyles.placeholder} />
    </div>
  );
});
